.list {
	margin: 0 0 20px;
	&-item {
		position: relative;
		padding: 0 0 0 20px;
		margin: 0 0 10px;
		&:before {
			content: "";
			position:absolute;
			background: #535251;
			top: 50%;
			left: 0;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			transform: translateY(-50%);
		}
	}
}